import { mapState } from "vuex";

export default {
    data() {
        return {
            detail: {}
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
        })
    },
    methods: {
        toIndex() {
            uni.reLaunch({
                url:'/pages/index/index'
            })
        },
    },

    onLoad(options) {
        this.$request({
            url: this.$api.teller.search,
            data: {
                pay_code: options.code
            },
            method: 'post'
        }).then(response=>{
            this.$hideLoading();
            if(response.code == 0) {
                this.detail = response.data;
            }
        }).catch(response => {
            this.$hideLoading();
        });
    }
}
